			<img src="../../static/img/m-banner1.webp" class="img-responsive m-bg" />
		</div>
		<div class="container center bread-nav">
			<span>当前位置:</span><span><router-link to="/sc_info">学院概况</router-link></span><span>></span><span><router-link to="/campus_style">校园文体</router-link></span><span>></span><span><router-link to="/campus_style">集体建设</router-link></span><span>></span><span><a  href="javascript:void(0);">正文</a></span>
		</div>
		<div class="container center info-content">
			<div class="col-lg-3 col-md-3 col-sm-12 col-xs-12">
				<!--pc-->
				<div class="pc-item-nav">
					<img src="../../static/img/item2.png" class="img-responsive" />
					<ul class="list-unstyled">
						<li>
							<router-link to ="/campus_style">
								<i class="iconfont icon-sjx-r"></i><span>主题教育</span>
							</router-link>
						</li>
						<li class="active">
							<router-link to ="/co_construction">
								<i class="iconfont icon-sjx-r"></i><span>集体建设</span>
							</router-link>
						</li>
						<li>
							<router-link to ="/community_mien">
								<i class="iconfont icon-sjx-r"></i><span>社团风采</span>
							</router-link>
						</li>
						<li>
							<router-link to="/campus_tour">
								<i class="iconfont icon-sjx-r"></i><span>校园风貌</span>
							</router-link>
						</li>
					</ul>
					<div class="zhaosheng">
						<a href="#">
							<img src="../../static/img/zhaosheng1.png" class="img-responsive" />
						</a>
						<a href="#">
							<img src="../../static/img/zhaosheng2.png" class="img-responsive" />
						</a>
					</div>
				</div>
				<!--mobail-->
				<div class="m-item-nav">
					<ul>
						<li>
							<router-link to ="/campus_style">
								<span>主题教育</span>
							</router-link>
						</li>
						<li class="active">
							<router-link to ="/co_construction">
								<span>集体建设</span>
							</router-link>
						</li>
						<li>
							<router-link to ="/community_mien">
								<span>社团风采</span>
							</router-link>
						</li>
						<li>
							<router-link to="/campus_tour">
								<span>校园风貌</span>
							</router-link>
						</li>
					</ul>
				</div>	
			</div>
			<div class="col-lg-9 col-md-9 col-sm-12 col-xs-12 info">
				<div class="info-title">
					<h4>集体建设</h4>
				</div>
				<div class="news">
					<span class="h3">{{news.title}}</span>
					<div class="news-info">
						<span>时间：{{news.add_time}}</span>
						<span>来源：{{news.origin}}</span>
					</div>
					<div v-html="news.content"></div>
				</div>
				
				<!--fenye-->
				<nav class="m-page" aria-label="...">
				  <ul class="pager">
				    <li><a href="#">&laquo;上一篇</a></li>
				    <li><a href="#">下一篇&raquo;</a></li>
				  </ul>
