			</div>
		</div>
	</div>
</template>

<script>
export default {
	data(){
		return{
			news:''
		}
	},
	created:function(){
    	var _self = this;
    	var newsId = this.$route.params.id;
    },
	beforeRouteEnter (to, from, next) {
		next(vm => {
		    var _self = vm;
			var newsId = vm.$route.params.id;
	　　　	var url="http://hh.c1903.com/index/api/artdetail";            
		  	vm.$http.post(url,{token:'xueyuanyi',code:'1a11c6a0e49a916429c1719fa8d587e2',type:'7',id:newsId}
				).then(function (data) {
	    		var json = data.body;             
		  		_self.news = json;
		  		this.$nextTick(() => {
					document.title=_self.news.title+"_扬州中瑞酒店职业学院互联网学院";
					//console.log(document.title);
				})
		  	});
	  })
	},
    watch: {
        '$route': function () {
            this.newsId = this.$route.params.id;
        }
    },
	activated(){
		 document.documentElement.scrollTop = 0
	}
}
</script>

