<template>
  <div id="header">
  	<div class="pc_top">
			<div class="container">
				<div class="logo">
					<router-link to="/">
						<img src="../../static/img/logo.png"/>
					</router-link>
				</div>
				<div class="top_info">
					<div>
						<span class="active">学院微信<div><span></span><img src="../../static/img/qcore.png" /></div></span>
						<div></div>
						<span>教育部院校代码：</span>
						<i>14528</i>
					</div>
				</div>
				
			</div>
		</div>
    <nav class="pc_nav">
			<div class="container">
				<ul class="list-inline first-nav">
					<li @click="selected(item.name,item.mca)" :class="{active: activeName == item.name || (uri == 'index' && index == 0 )}" ref="li" v-for="(item, index) in navList" >
						<router-link to="/" class="text-center">{{item.name}}</router-link>
						<ul class="second-nav list-unstyled">
							<li v-for="item1 in item._data" @click.stop="selected(item1.name,item1.mca)" >
								<a class="text-center">{{item1.name}}</a>
							</li>
						</ul>	
					</li>
				</ul>	
			</div>
		</nav>
		<!--mobail-->
		<div class="m-top">
			<router-link to="/">
				<img src="../../static/img/m-logo1.png" />
			</router-link>
			<img src="../../static/img/nav.png" class="m-nav-botton" data="close"/>
		</div>
		<div class="m-logo">
			<img src="../../static/img/m-logo.png"/>
		</div>
		<nav class="m-nav" id="nav">
			<ul>
				<li @click="selected(item.name,item.mca,'mobail')"  v-for="item in navList" >
					<a><i class="iconfont" :class="item.ico"></i><span>{{item.name}}</span></a>
					<ul class="m-second-nav list-unstyled" :class="{active: activeName == item.name }">
						<li v-for="item1 in item._data" @click.stop="selected(item1.name,item1.mca,'child')" ><a><span>{{item1.name}}</span></a></li>
					</ul>
				</li>
			</ul>
		</nav>
  </div>
</template>

<script>
export default {
	data(){
        return{
        	isShow: false,
        	navList: [],
        	gameName:'',
          activeName: '', 
          status: false,
          uri: ''
        }
    },
    created:function(){
    	this.uri = this.$route.path.slice(1);
    	var _self = this;
　　　	var url="http://hh.c1903.com/index/api/menu";            
		  this.$http.post(url,{token:'xueyuanyi',code:'1a11c6a0e49a916429c1719fa8d587e2'}
			).then(function (data) {
        var json = data.body;             
		  	_self.navList = json;
		  	_self.navList = _self.navList[0]._data;
    	});      
    },
    watch: {
    	$route (to, from) {
    		this.uri = this.$route.path;
    		if( this.uri == "/"){
    			document.title = "扬州中瑞酒店职业学院互联网学院";
    		}
    	}
    },
		methods: {
	    selected: function(name,url,type) {
	      this.activeName = name;
	      this.$router.push({ path: url });
	      if(type == "mobail"){
	      	if( name == "网站首页" || name == "师资队伍" || name == "企业课堂" || name == "招生就业" || name == "联系我们"){
	      		$(".m-nav").removeClass("m-nav-move");
						$("body").removeClass("body");
	      	}
	      }
	      if(type == 'child'){
	      	$(".m-nav").removeClass("m-nav-move");
					$("body").removeClass("body");
	      }
	    }
	 }
	}
</script>





