<script>
import Swiper from 'Swiper';
var swiper,hList=[];
export default {
	data(){
        return{
        	totalList: [],
        	//学院动态
        	dynList:[],
        	//公示公告
        	noticeList:[],
        	lunList:[],
        	hList:[],
        	
        	courseImgList:[
        		{ img:"../../static/img/bg6.png",text:"上下课指纹打卡"},
        		{ img:"../../static/img/bg7.png",text:"工业沙盘实战模拟"},
        		{ img:"../../static/img/bg8.png",text:"企业责任制管理"},
        		{ img:"../../static/img/bg9.png",text:"工业沙盘实战模拟"}
        	],
        	majorList:[
        		{ img : "../../static/img/bg1.png",text:"会计",url:"/special_major_accouting" },
        		{ img : "../../static/img/bg2.png",text:"艺术设计",url:"/special_major_graphdesign" },
        		{ img : "../../static/img/bg3.png",text:"室内设计",url:"/special_major_insidedesign" },
        		{ img : "../../static/img/bg4.png",text:"软件技术",url:"/special_major_soft" },
        		{ img : "../../static/img/bg5.png",text:"计算机网络技术",url:"/special_major_net" },
        	],
        	styleList:[
        		{ img : "../../static/img/bg11.png",text:"主题教育",url:"/campus_style" },
        		{ img : "../../static/img/bg12.png",text:"集体建设",url:"/co_construction" },
        		{ img : "../../static/img/bg13.png",text:"社团风采",url:"/community_mien" },
        		{ img : "../../static/img/bg14.png",text:"校园风貌",url:"/campus_tour" },
        	],
        	linkList:[
        		{ img:"../../static/img/link1.png"},
        		{ img:"../../static/img/link2.png"},
        		{ img:"../../static/img/link3.png"},
        		{ img:"../../static/img/link4.png"},
        	]
        }
    },
    mounted() {
    	
    },
	created:function(){
    	var _self = this;
　　　	var url="http://hh.c1903.com/index/api/article";            
		  this.$http.post(url,{token:'xueyuanyi',code:'1a11c6a0e49a916429c1719fa8d587e2',type:'1,2'}
			).then(function (data) {
       		var json = data.body;             
		  	_self.totalList = json;
			for (var i = 0;i<_self.totalList.length;i++) {
				if(_self.totalList[i].type == "1"){
					if(_self.dynList.length<3){
						_self.dynList.push(_self.totalList[i]);
						var dateTime = _self.totalList[i].add_time;
						dateTime =dateTime.split("-");
						_self.totalList[i].day = dateTime[2];
						_self.totalList[i].mouth = dateTime[1];
						_self.totalList[i].year = dateTime[0];
					}
				}
				if(_self.totalList[i].type == "2"){
					if(_self.noticeList.length<6){
						_self.noticeList.push(_self.totalList[i]);
					}
				}
			}
    	});
    	var url1="http://hh.c1903.com/index/api/banner";            
		  this.$http.post(url1,{token:'xueyuanyi',code:'1a11c6a0e49a916429c1719fa8d587e2'}
			).then(function (data) {
        	var json = data.body;             
		  	_self.lunList = json;
			for(var i=0;i<_self.lunList.length;i++){
				hList.push(_self.lunList[i].title);
			}
			this.$nextTick(() => {
				this.lunbo();				
			})
    	});          
    },
    methods: {
	    forward: function(n) {
	    	swiper.slideTo(n, 1000, false);
	    },
	    lunbo:function(){
	    	function active(n){
		  	 	$(".index").find("span").removeClass("active");
		  	 	$(".index").find("span").eq(n).addClass("active");
		  	 	$(".pagination h5").text(hList[n]);
		  	}
	    	swiper = new Swiper('.swiper-container', {
	            pagination: '.swiper-pagination',
		        paginationClickable: true,
		        nextButton: '.swiper-button-next',
        		prevButton: '.swiper-button-prev',
		        loop:'true',
		        lazyLoading : true,
		        autoplay : 6000,
	            onTouchEnd: function(swiper){
			      active(swiper.realIndex);
			    },
			    onSlideChangeEnd: function(swiper){
				    active(swiper.realIndex);
			    }
	        });
	        $(".swiper-button-next,.swiper-button-prev").click(function(){
	        	active(swiper.realIndex);
	        });
	        $(".pagination span").click(function(){
		  	 	$(".index").find("span").removeClass("active");
		  	 	var num = $(this).index();
		  	 	swiper.slideTo(num+1, 1000, false);
		  	 	active(num);
		  	});
	    },
	    select(uri){
	    	this.$router.push({ path: uri });
	    },
	    activated(){
	    	console.log(1)
	    	document.title = "扬州中瑞酒店职业学院互联网学院";
	    },
	    watch:{
		    $route (to, from) {
    		console.log(this.$route.path);
    	    }
	    },
		activated(){
			 document.documentElement.scrollTop = 0
		}
	}
}
</script>

<style>
	img[lazy=loading] {
	    /*width: 100px;*/
	    background-position:center center!important;
	    background: url("../../static/img/spinner.svg");
	    background-size: 65px 65px !important;
	    background-repeat:no-repeat;
	    -webkit-background-size: cover;
	    -moz-background-size: cover;
	    -o-background-size: cover;
	    background-size: cover;
	    height: 131px;
        margin: 0 auto;
	}
</style>


