import Vue from 'vue'
import Router from 'vue-router'
import Index from '../components/index'
//学院概况
import Scinfo from '../components/sc_info'
//学院动态
import Scdyn from '../components/sc_dynamics'
import Notice from '../components/public_notice'
import Edu from '../components/edu_dyn'
//特色专业——会计
//import SpeMajAccouting from '../components/special_major_accouting'
//import SpeMajGraph from '../components/special_major_graphdesign'
//import SpeMajInside from '../components/special_major_insidedesign'
//import SpeMajSoft from '../components/special_major_soft'
//import SpeMajNet from '../components/special_major_net'
//师资队伍
//import Teacher from '../components/teachers'
//import TeacherA from '../components/teachers1'
//import TeacherC from '../components/teachers3'
//企业课堂
//import Course from '../components/course'
//联系我们
import Contact from '../components/contact'

//校园文体-主题教育
//import Campus from '../components/campus_style'
//import Construction from '../components/co_construction'
//import Mien from '../components/community_mien'
//校园文体-校园风貌
//import Tour from '../components/campus_tour'
//校园文体-校园风貌


//news_page
//实时动态
import News from '../components/news_page'
//公示公告
import News1 from '../components/news_page1'
//主题教育
import News2 from '../components/news_page2'
//集体建设
import News3 from '../components/news_page3'
//社团风采
import News4 from '../components/news_page4'
//教育动态
import News5 from '../components/news_page5'
//lu you
Vue.use(Router)

export default new Router({
	//mode:'history',
  routes: [
	  
	  {
	  	path: '/',
	  	component: Index
	  },
    {
      path: '/sc_info',
      component: Scinfo
    },
    {
    	path: '/sc_dynamics',
      component: Scdyn
    },
    {
    	path: '/public_notice',
      component: Notice
    },
    {
    	path: '/edu_dyn',
      component: Edu
    },
    {
    	path: '/special_major_accouting',
    	component: resolve => require(['../components/special_major_accouting'],resolve)
    },
     {
    	path: '/special_major_graphdesign',
    	component: resolve => require(['../components/special_major_graphdesign'],resolve)
    },
     {
    	path: '/special_major_insidedesign',
    	component: resolve => require(['../components/special_major_insidedesign'],resolve)
    },
     {
    	path: '/special_major_soft',
    	component: resolve => require(['../components/special_major_soft'],resolve)
    },
     {
    	path: '/special_major_net',
    	component: resolve => require(['../components/special_major_net'],resolve)
    },
    {
    	path: '/teachers',
    	component: resolve => require(['../components/teachers'],resolve)
    },
    {
    	path: '/teachers1',
    	component: resolve => require(['../components/teachers1'],resolve)
    },
    {
    	path: '/teachers3',
    	component: resolve => require(['../components/teachers3'],resolve)
    },
    {
    	path: '/course',
    	component: resolve=> require(['../components/course'],resolve)
    },
    {
    	path: '/contact',
    	component: Contact
    },
    {	
    	path: '/campus_style',
    	component: resolve => require(['../components/campus_style'],resolve)
    },
     {
    	path: '/co_construction',
    	component: resolve => require(['../components/co_construction'],resolve)
    },
     {
    	path: '/community_mien',
    	component: resolve => require(['../components/community_mien'],resolve)
    },
    {
    	path: '/campus_tour',
    	component: resolve => require(['../components/campus_tour'],resolve)
    },

    {
    	path: '/campus_tour1',
    	component: resolve => require(['../components/campus_tour1'],resolve)
    	
    },
    {
    	path: '/campus_tour2',
    	component: resolve => require(['../components/campus_tour2'],resolve)
    },
    {
    	path: '/campus_tour3',
    	component: resolve => require(['../components/campus_tour3'],resolve)
    },
    {	///sc_dynamics
    	path: '/news_page/:id',
    	component: News,
    	name:'news'
    },
    {	//public
    	path: '/news_page1/:id',
    	component: News1,
    	name:'news1'
    },
    {	///co
    	path: '/news_page2/:id',
    	component: News2,
    	name:'news2'
    },
    {	///zhuti
    	path: '/news_page3/:id',
    	component: News3,
    	name:'news3'
    },
     {	///community_mien
    	path: '/news_page4/:id',
    	component: News4,
    	name:'news4'
    },
    {	///edu_dyn
    	path: '/news_page5/:id',
    	component: News5,
    	name:'news5'
    },
    
  ]
})



